pr(2, 7);

//3
function pri(A, B) {
    let product = 1;
    for (let i = A; i <= B; i++) {
      product *= i;
    }
    return product;
    }
console.log("Задание 3:")
console.log("Произведение чисел от 3 до 5:", pri(3, 5));

//4
function prim(N) {
    let sum = 0;
    for (let i = 1; i <= N; i++) {
        sum += 1 / i;
    }
    return sum;
}
console.log("Задание 4:")
console.log("Сумма дробей:", prim(2));

//5
function prime(price) {
    for (let i = 1; i <= 10; i++) {
      console.log(`${i} кг конфет: ${price * i} рублей`);
    }
}
console.log("Задание 5:")
prime(100);

//6
function primer(A, B) {
    let product = 1;
    for (let i = A; i <= B; i++) {
        // Проверяем перед умножением
        if (product * i > 100) {
            break;
        }
        product *= i;
    }
    return product;
}
console.log("Задание 6:")
console.log("Произведение чисел от 2 до 7:", primer(2, 7))

//7
function primerr(N) {
    let sum = 0;
    let h = 1;
    for (let i = 1; i <= N; i++) {
        if (i % 2 === 0) {
            sum += i;
        } else {
            h *= i;
        }
    }
    console.log(`Сумма четных чисел: ${sum}`);
    console.log(`Произведение нечетных чисел: ${h}`);
}
console.log("Задание 7:");
let N = parseInt(prompt("Введите положительное число N:"));
primerr(N);